import {deepAssign} from "./utils"
class Config {
    options = {}
    constructor() {
        // 
        this.options = {
            class:{
                /**IPageclass */
                IPageRoot:'ipage',
                /** ISearchclass */
                ISearchRoot:'isearch',
                /** ITableclass */
                ITableRoot:'itable',
                /** IFormclass
                 *  
                 */
                IFormRoot:'iform',
                /** class */
                IPageOperation:'ipage_operation', 
                /** class */
                tableWrap:'ipage_tableWrap', 
                /** class */
                IPagePagination:'ipage_pagination',  
            },
            /// IPage 
            /**  */
            response:{
                /** ITabledata */
                data: 'data.records',
                /** total */
                total: 'data.total',
                /** 
                 * 
                 * data: 
                 * total: 
                */
                beforeFunc: null,
            },
            /**  */
            search:{
                /** page=offset= */
                mode:'page',
                /**  */
                pageSize:'pageSize',
                pageNo:'pageNo',
                /**   */
                limit:'limit',
                offset:'offset',
                /** 
                 * @param {object} params 
                 * @returns {object} 
                 */
                beforeFunc: null, 
            },
            
            /** IPageITable
             * parent=<>
             * data=
             *  */
            tableHeightMode:'parent', // 
            /**  */
            tableHeight:0,
            /** IPageel-dialog */
            dialogProps:{
                width: '50%',
                appendToBody: true,
            },
            /** IPageel-dialog */
            dialogOn:{ },
            
            /** ITable  */
            columnButtonProps:{
                columnProps: {
                    label: '',
                    minWidth: 140,
                    fixed: 'right',
                },
            },
            /** el-pagination el-pagination */
            paginationProps:{
                layout:"total, sizes, prev, pager, next, jumper"
            },
            /** toolbarRenderCell */
            toolbarButton:{
                slot:'button',
                props:{
                    size:'small',
                    type:'text',
                }
            },
            /** RenderCell 
             * addButton,deleteButtoncolumnButtons
            */
            button:{
                slot: 'button',
                props: {
                    size: 'small',
                    type: 'text',
                },
            },
            /** addButtonRenderCell
             * IPageaddButtonon:{click:fn}
             */
            addButton:{
                id: 'add',
                tip: '',
                props: {
                    size: 'small',
                    type: 'primary',
                    icon: 'el-icon-plus',
                }
            },
            /** editButtonRenderCell
             * IPageeditButtonon:{click:fn}
             */
            editButton:{
                id: 'edit',
                tip: '',
            },
            /** deleteButtonRenderCell
             * IPagedeleteButtonon:{click:fn}
             */
            deleteButton:{
                id: 'delete',
                tip: '',
            },
            /** refreshButtonRenderCell
             * IPagerefreshButtonon:{click:fn}
             */
            refreshButton:{
                id:'refresh',
                props:{
                    icon:'el-icon-refresh'
                },
            },
            /// IForm 
            /** IFromel-formclass */
            formClass:"iform", // 
            /** IFromel-form */
            formProps:{
                "label-width": "120px"
            },
            /** IFromel-form-item */
            formOn:{ },
            /** IFromRenderCellarray */
            expandButtons:[],

            ////// RenderCell
            /** IFrom */
            showSubmitButton:true,
            submitTitle:"",
            cancelTitle:"",
            submitButtonProps:{
                type: "primary"
            },
            showCancelButton:false,
            cancelButtonProps:{
                type: "default"
            },
            /**  RenderCell false */
            formSubmitButton:{
                slot: 'button',
                props: {
                    type: 'primary',
                }
            },
            /**  RenderCell false */
            formCancelButton:false,
            submitPreventRepeat:false,
            /// ISearch 
            /** ISearchel-form */
            searchFormProps:{
                inline:true
            },
            searchInput:{
                props:{
                    size:'small',
                    clearable:true,    
                }
            },
            searchButton:{
                props:{
                    size:'small',   
                }
            },
            // RenderCell
            showSearchButton:true,
            searchButtonProps:{
                type: "primary",
                icon: 'el-icon-search',
                size:"small"
            },
            showResetButton:true,
            resetButtonProps:{
                type: "default",
                icon: '',
                size:"small"
            },
            // 
            searchSubmitButton:{
                slot: 'button',
                props: {
                    icon: 'el-icon-search',
                    type: 'primary',
                    size:"small"
                }
            },
            searchResetButton:{
                slot: 'button',
                props: {
                    type: 'default',
                    size:"small"
                }
            },
            /** more  */
            allinMore:false,
            /// ITable 
            tableProps:{},
            tableOn:{},
            /** jsxvnode */
            extendedRenderCell:{
                
            }
        }   
    }
    /** options */
    set(options) {
        this.options = deepAssign(this.options,options)
    }
    
    /** deepPathstring
     * 'dialogProps.appendToBody' this.options.dialogProps.appendToBody
     * 
     */
    get(deepPath) {
        if (deepPath) {
            const paths = deepPath.split('.');
            let result = this.options;
            for (let path of paths) {
                // todo 
                result = result[path];
            }
            return result;
        } else {
            return this.options;
        }
    }
}
export const config = new Config()