-->
<template>
  <el-select
    v-model="customValue"
    style="width: 100%"
    v-bind="$props"
    filterable
    remote
    reserve-keyword
    :placeholder="defaultValue"
    :remote-method="handleRemoteSearch"
    v-el-select-loadmore="that"
    @change="handleChange"
    @clear="handleClear"
    @visible-change="handleVisibleChange"
  >
    <div :class="[customLoading ? 'selectLoad' : 'noData']">
      <el-option
        v-for="(item, index) in options"
        :key="item[keyField] ? item[keyField] : index"
        :label="getLabel(item)"
        :value="item[valueField]"
        :disabled="disabledFunc(item)"
      />
    </div>
  </el-select>
</template>
<script>
import { Select } from 'element-ui';
const { props: elSelectProps } = Select;
export default {
  name: 'RSelectLoadMore',
  props: {
    ...elSelectProps,
    value: {
      type: [String, Object, Number, Boolean, Array],
      default: ''
    },
    service: {
      type: Function,
      default: () => () => {}
    },
    labelField: {
      type: String,
      default: 'name'
    },
    valueField: {
      type: String,
      default: 'value'
    },
    keyField: {
      type: String,
      default: ''
    },
    /**
     *  keyword
     */
    searchKey: {
      type: String,
      default: 'keyword'
    },
    /**
     * 
     */
    additionalParams: {
      type: Object,
      default: () => ({})
    },
    /**
     * truefalse
     */
    disabledFunc: {
      type: Function,
      default: () => false
    },
    /**
     * label - 
     */
    labelFieldArr: {
      type: Array,
      default: () => []
    },
    /**
     * 
     */
    defaultValue: {
      type: String,
      default: ''
    },
    /**
     * id
     */
    isCallBackShow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      customValue: this.value,
      params: {
        pageNo: 1,
        pageSize: 10,
        keyword: ''
      },
      customLoading: false,
      // 
      that: this,
      // 
      isSuccess: true,
      // 
      isEnd: true,
      options: [],
      queryWord: '',
      total: 0,
      isUserSelected: false //,handleRemoteSearch
    };
  },
  directives: {
    'el-select-loadmore': {
      bind (el, binding) {
        const componentInstance = binding.value;
        const scrollDom = el.querySelector('.el-select-dropdown__wrap');
        if (scrollDom) {
          scrollDom.addEventListener('scroll', () => {
            if (scrollDom.scrollTop + scrollDom.clientHeight >= scrollDom.scrollHeight - 4) {
              componentInstance.handleScrollToEnd();
            }
          });
        }
      }
    }
  },
  watch: {
    value: {
      handler (value) {
        if (value && !this.multiple && !this.isUserSelected) {
          this.isUserSelected = false; // 
          this.getList(value);
        }
        this.customValue = value;
      },
      immediate: true
    },
    customValue (val) {
      this.$emit('input', val);
    }
  },
  computed: {
    noData () {
      return this.options.length === this.total;
    }
  },
  created () {
    this.init();
    let searchValue = '';
    if (this.isCallBackShow) {
      searchValue = this.customValue;
    }
    this.getList(searchValue);
  },
  methods: {
    getValueByPath (obj, path) {
      const keys = path.split('.');
      let result = obj;
      for (let key of keys) {
        result = result[key];
        if (!result) return null;
      }
      return result;
    },
    getLabel (item) {
      if (this.labelFieldArr.length === 0) {
        return item[this.labelField];
      } else {
        return this.labelFieldArr.map(field => this.getValueByPath(item, field)).join(' - ');
      }
    },
    init () {
      this.isUserSelected = false;
      this.total = 0;
      this.queryWord = '';
      this.params.pageNo = 1;
      this.options = [];
    },
    handleVisibleChange (val) {
      // webview
      this.init();
      if (val) {
        this.customLoading = true;
        this.getList();
      }
    },
    handleClear () {
      this.options = [];
      this.params.pageNo = 1;
      this.getList();
    },
    handleChange (args) {
      this.$emit('input', args);
      if (Array.isArray(args)) {
        const selected = this.options.filter(it => args.includes(it[this.valueField]));
        this.$emit('getCurrentItem', selected, args);
      } else {
        const currentItem = this.options.find(it => it[this.valueField] === args);
        this.$emit('getCurrentItem', currentItem, args);
      }
      this.isUserSelected = true; // 
      // isUserSelected
      setTimeout(() => {
        this.isUserSelected = false;
      }, 300); // 300
    },
    async getList (query) {
      try {
        this.customLoading = true;
        let queryWord = query || this.queryWord;
        const params = { ...this.params, ...this.additionalParams, [this.searchKey]: queryWord };
        const data = await this.service(params);
        this.options = [...this.options, ...data.records];
        this.total = data.total;
        // 
        // const isValueInOptions = this.options.some(option => option[this.valueField] === this.customValue);
        // if (!isValueInOptions && this.defaultValue) {
        //   // this.customValue = this.defaultValue;
        // }
      } catch (error) {
        this.$message.error(error.message || '');
      } finally {
        this.customLoading = false;
      }
    },
    handleRemoteSearch (query) {
      // 
      if (this.isUserSelected) {
        this.isUserSelected = false; // 
        return;
      }
      // 
      this.options = [];
      this.queryWord = query;
      this.params.pageNo = 1;
      this.getList(this.queryWord);
    },
    handleScrollToEnd () {
      if (!this.customLoading && this.options.length < this.total) {
        this.params.pageNo++;
        this.getList(this.queryWord);
      }
    }
  }
};
</script>
<style lang="scss">
.selectLoad:after {
  position: relative;
  z-index: 1000;
  content: '...';
  display: inline-block;
  text-align: center;
  color: #979797;
  width: 100%;
  line-height: 30px;
  overflow: hidden;
  font-size: 13px;
}
.noData:after {
  position: relative;
  z-index: 1000;
  content: '';
  color: #979797;
  display: inline-block;
  text-align: center;
  width: 100%;
  line-height: 30px;
  overflow: hidden;
  font-size: 13px;
}
</style>
